<template>
  <div class="itemBox">
    <p class="title" @click="showData">图片广告</p>

    <!-- //0是轮播广告 1一行一个
          bannerType: 0,
          // 广告样式
          advertisingType: 0,
          //卡片倒角  0是直角 1圆角
          chamfering: 0,
          chamferingStyle: "",
          //卡片投影  0无投影 1有投影
          projection: 0,
          projectionStyle: "",
          //图片填充  0是填充 1周边留白
          fill: 0,
          fillStyle: "",
          //图片比例  0是2:1 1是4：3
          ratio: 0,
    ratioStyle: "",-->
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="选择模板" class="advertisingType">
        <img
          :class="itemData.bannerType==0?'selectTemplateClass':''"
          @click="itemData.bannerType=0"
          src="@/assets/decorateBox/advertisingType1.png"
          alt
        />
        <img
          :class="itemData.bannerType==1?'selectTemplateClass':''"
          @click="itemData.bannerType=1"
          src="@/assets/decorateBox/advertisingType2.png"
          alt
        />
      </el-form-item>

      <el-form-item label="广告样式" class="navigation" v-if="itemData.bannerType==0">
        <el-radio-group v-model="itemData.advertisingType">
          <el-radio :label="0">样式1</el-radio>
          <el-radio :label="1">样式2</el-radio>
          <el-radio :label="2">样式3</el-radio>
          <el-radio :label="3">样式4</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="展示样式" class="showType">
        <el-radio-group v-model="itemData.chamfering" @change="showType(0)">
          <span class="showClass">卡片倒角</span>
          <el-radio :label="0">直角</el-radio>
          <el-radio :label="1">圆角</el-radio>
        </el-radio-group>
        <el-radio-group v-model="itemData.projection" @change="showType(1)">
          <span class="showClass">卡片投影</span>
          <el-radio :label="0">无投影</el-radio>
          <el-radio :label="1">有投影</el-radio>
        </el-radio-group>
        <el-radio-group v-model="itemData.fill" @change="showType(2)">
          <span class="showClass">图片填充</span>
          <el-radio :label="0">填充</el-radio>
          <el-radio :label="1">周边留白</el-radio>
        </el-radio-group>
        <el-radio-group v-model="itemData.ratio" @change="showType(3)">
          <span class="showClass">图片比例</span>
          <el-radio :label="0">2:1</el-radio>
          <el-radio :label="1">4:3</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- {{itemData}} -->
      <el-form-item label="背景图" class="bgsetting">
        <ul
          v-for="(item,index) in itemData.bannerBg"
          :key="index"
          @mouseover="mouseOver(index)"
          @mouseleave="mouseLeave"
          v-show="itemData.bannerBg.lenght!=0"
        >
          <li class="showImg">
            <span class="selectBg">选择图片</span>
            <span class="showImgbtn">
              <img :src="item.imageUrl" alt />
              <div class="replaceImg">
                <p>
                  <el-button size="mini" @click="addBanner(1,index)">更换图片</el-button>
                </p>
                <p class="notis">建议尺寸750*375像素</p>
              </div>
            </span>
          </li>
          <li>
            添加链接
            <!-- <el-select v-model="item.status" placeholder="请选择链接方式">
              <el-option label="商品链接" @click.native="selsectStatus(index)" :value="0"></el-option>
              <el-option label="微页面链接" @click.native="microPage(index)" :value="1"></el-option>
              <el-option label="首页链接" @click.native="homeLink(index)" :value="2"></el-option>
            </el-select>-->

            <el-dropdown trigger="click" placement="bottom">
              <el-button plain>
                <!-- 请选择链接方式 -->
                {{item.urlInfo==''?'请选择链接方式':pname(index)+item.urlInfo}}
                <i
                  class="el-icon-arrow-down el-icon--right"
                ></i>
              </el-button>
              <el-dropdown-menu :data-index="index" slot="dropdown">
                <el-dropdown-item @click.native="selsectStatus(index)">商品链接</el-dropdown-item>
                <el-dropdown-item @click.native="microPage(index)">微页面链接</el-dropdown-item>
                <el-dropdown-item @click.native="homeLink(index)">首页链接</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </li>
          <li class="lastDel">
            <img
              class="delBtn"
              :class="hoverIndex==index?'delBtnShow':''"
              src="@/assets/decorateBox/goods/close.png"
              @click="delBgs(index)"
              alt
            />
          </li>
        </ul>
        <div class="goodsUpload" @click="addBanner(0,itemData.bannerBg.length +1)">
          <div class="el-upload__text">
            <span>+</span>添加背景图
          </div>
          <div class="el-upload__text">建议宽度为750像素</div>
        </div>
      </el-form-item>
    </el-form>
    <!-- 图片库 -->
    <el-dialog
      title="图库列表"
      :visible.sync="dialogVisible"
      width="76%"
      :close-on-click-modal="false"
      :append-to-body="true"
      class="add-dialog"
      top="6vh">
      <imageMaterial
        :limitType="true"
        :getPost="getPost"
        :multiple="false"
        :totalNum="1"
        :selectNum="0"
        @onlyclose="submitFormqx"
        @subMit="submitForm"
      ></imageMaterial>
    </el-dialog>
    <!-- 微页面弹出框 -->
    <singleVpage :vpageShow="vpageShow" @vpageClose="vpageClose" @vpageFun="vpageFun"></singleVpage>
    <!-- 商品弹出框 -->
    <singleCommodity :commodityShow="commodityShow" :commodityIds="commodityIds" @commodityClose="commodityClose" @commodityFun="commodityFun"></singleCommodity>
  </div>
</template>

<script>
import { getProductCategoryShopAllList } from "@/api/commodity/commodityClassify";
import { getProductInfoList } from '@/api/commodity/commodityList'
import imageMaterial from "@/components/shopMaterial/imageMaterial";
import singleVpage from '@/components/popover/singleVpage';
import singleCommodity from '@/components/popover/singleCommodity';
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      commodityShow:false,
      commodityIds:'',

      hoverIndex: -1, //hover的index
      replaceIndex: 0, //替换选中的判断
      imgIndex: -1,
      dialogVisible: false,
      shopId: "",
      getPost: true,

      // 商品的
      loading: false,
      loadingbut: false, //审核提交加载中
      loadingbuttext: "确 定",
      pageNumber: 5, //默认分页条数
      nowPage: 1, //默认第几页
      total: 1,
      tableData: [],
      selectData: [],
      search: {
        name: "",
        shopCategoryId: ""
      },
      search1: {
        name: "",
        shopCategoryId: ""
      },
      bgSelectIndex: 0,
      multipleSelection: [], //选中的数据

      // 微页面的

      pageNumber2: 10, //默认分页条数
      nowPage2: 1, //默认第几页
      total2: 1,
      search2_1: {
        name: ""
      },
      search2: {
        name: ""
      },
      multipleSelection2: [] ,//选中的数据,

      vpageShow:false,
    };
  },
  components: {
    imageMaterial,
    singleVpage,
    singleCommodity
  },
  created(){
    console.log('图片广告回显数据',this.itemData)
  },
  methods: {
    showData(){
      console.log(this.itemData.bannerBg)
    },
    // 商品
    commodityClose(){
      this.commodityShow = false;
    },
    commodityFun(e){
      let i = this.bgSelectIndex;
      console.log('this.bgSelectIndex',i)
      //商品id
      this.itemData.bannerBg[i].goodIdOrUrl = e[0].productId;
      //商品名称
      this.itemData.bannerBg[i].urlInfo = e[0].productName;
      this.itemData.bannerBg[i].status = 0;
      this.commodityShow = false;
    },
    // 微页面
    microPage(index){
      this.bgSelectIndex = index;
      this.vpageShow = true;
    },
    // 微页面弹出框 操作
    vpageClose(){
      this.vpageShow = false;
    }, 
    vpageFun(e){
      //商品id
      let i = this.bgSelectIndex
      console.log('this.bgSelectIndex',i)
      this.itemData.bannerBg[i].goodIdOrUrl = e[0].vpageId;
      this.itemData.bannerBg[i].urlInfo = e[0].vpageName;
      this.itemData.bannerBg[i].status = 1;
      this.vpageShow = false;
    },
    // 
    pname(index) {
      let name = "";
      let box1 = this.itemData.bannerBg[index].status;
      if (box1 == 0) {
        name = "商品链接 | ";
      } else if (box1 == 1) {
        name = "微页面链接 | ";
      } else {
        name = "";
      }
      return name;
    },
    //展示样式
    showType(e) {
      console.log(e);
      switch (e) {
        case 0:
          if (this.itemData.chamfering == 0) {
            this.itemData.chamferingStyle = 0;
          } else {
            this.itemData.chamferingStyle = "8px";
          }
          break;
        case 1:
          if (this.itemData.projection == 0) {
            this.itemData.projectionStyle = "0px 0px 0px 0px";
          } else {
            this.itemData.projectionStyle ="4px 4px 4px 4px rgba(153,153,153,0.2)";
          }
          break;
        case 2:
          if (this.itemData.fill == 0) {
            this.itemData.fillStyle = 0;
          } else {
            this.itemData.fillStyle = "10px";
          }
          break;
      }
    },
    //弹窗取消按钮
    submitFormqx() {
      this.dialogVisible = false;
      this.replaceIndex = 0;
      this.imgIndex = -1;
    },
    //弹窗确定按钮
    submitForm(e) {
      console.log(e);
      // if (e.length != 0 && this.replaceIndex == 1) {
      //   //当是替换图片
      //   e.map(item => {
      //     this.$set(item, "urlInfo", "");
      //     this.$set(item, "goodIdOrUrl", "");
      //     this.$set(item, "title", "");
      //     this.$set(item, "status", "");
      //   });
      //   this.itemData.bannerBg[this.imgIndex] = e[0];
      // } else if (e.length != 0 && this.replaceIndex == 0) {
      //   //当是上传图片
      //   this.dialogVisible = false;
      //   e.map(item => {
      //     this.$set(item, "urlInfo", "");
      //     this.$set(item, "goodIdOrUrl", "");
      //     this.$set(item, "title", "");
      //     this.$set(item, "status", "");
      //     this.itemData.bannerBg.push(item);
      //   });
      // } else {
      //   this.$message.error("请选择图片");
      // }
      let obj = e[0];
      if(this.replaceIndex == 0){
        obj.urlInfo =  ''
        obj.goodIdOrUrl = ''
        obj.status = ''
      }
      if(this.replaceIndex == 1){
        obj.urlInfo = this.itemData.bannerBg[this.imgIndex].urlInfo || ''
        obj.goodIdOrUrl = this.itemData.bannerBg[this.imgIndex].goodIdOrUrl || ''
        obj.status = this.itemData.bannerBg[this.imgIndex].status || ''
      }
      console.log('this.imgIndex',this.imgIndex)
      this.itemData.bannerBg.splice(this.imgIndex,1,obj)
      this.dialogVisible = false;
      this.getPost = false;
    },

    // 弹窗并选择商品
    addBanner(e, index) {
      this.getPost = true;
      this.dialogVisible = true;
      this.replaceIndex = e;
      this.imgIndex = index;
    },

    // 移入事件
    mouseOver(index) {
      this.hoverIndex = index;
    },
    // 移出事件
    mouseLeave() {
      this.hoverIndex = -1;
    },
    // 删除商品
    delBgs(index) {
      this.itemData.bannerBg.splice(index, 1);
    },

    //商品的
    closed() {
      this.loadingbut = false;
      this.loadingbuttext = "确 定";
    },
    //选中下拉的商品
    selsectStatus(index) {
      this.bgSelectIndex = index;
      this.commodityShow = true;
    },

    //选中首页链接事件
    homeLink(index) {
      this.bgSelectIndex = index;
      this.itemData.bannerBg[index].goodIdOrUrl = "/index";
      this.itemData.bannerBg[index].urlInfo = "首页链接";
      this.itemData.bannerBg[index].status = 2;
    },

    //重置
    resetForm(formName) {
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
        this.search.name = "";
        this.search.shopCategoryId = "";
      });
    },
    // 搜索 - 点击搜索
    searchSubmit() {
      this.search.name = this.search1.name;
      this.search.shopCategoryId = this.search1.shopCategoryId;
      this.required(1, 5);
    }
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  padding: 20px 18px 24px 18px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.el-dialog__body {
  .pagination {
    padding-top: 20px;
  }
  .search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 20px;
    }
    .formBtnBox {
      margin-left: 20px;
    }
  }

  .bannerData {
    display: flex;
    flex-wrap: wrap;
    li {
      // width: 280px;
      box-sizing: border-box;
      padding: 6px;
      background-color: #f6f6f6;
      margin-right: 10px;
      margin-bottom: 20px;
      border: 1px solid #f6f6f6;
      cursor: pointer;
      transition: 0.2s;
      &:hover {
        background-color: #ecf5ff;
        border: 1px solid #a2cfff;
      }

      img {
        width: 260px;
        height: 166px;
        margin-bottom: 8px;
      }
      .sizes {
        color: #999;
      }
    }
    .clickLi {
      background-color: #ecf5ff;
      border: 1px solid rgba(0, 121, 253, 1);
    }
  }
}

.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 14px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    .el-radio {
      margin-left: 10px;
      margin-right: 9px;
    }
    .el-radio-group {
      display: flex;
    }
  }

  .el-dropdown {
    .el-button {
      width: 214px;
      margin-left: 10px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      border-radius: 2px;
    }
  }
  .showType {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    .el-radio {
      width: 88px;
      margin: 0;
    }
    .el-radio-group {
      margin-bottom: 8px;
    }
  }
  .marginType {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    li {
      position: relative;
      .el-slider {
        margin-left: 24px;
      }
      .marginTypeName {
        font-size: 12px;
        color: rgba(153, 153, 153, 1);
        margin-left: 3px;
        position: absolute;
        top: 5px;
      }
      .el-input__inner {
        height: 28px;
      }
      .el-slider__input {
        width: 90px;
      }
      .el-slider__runway.show-input {
        margin-right: 118px;
        margin-left: 50px;
      }
    }
  }
  .advertisingType {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    .el-form-item__content {
      display: flex;
      margin-left: 10px;
      img {
        margin-right: 24px;
        border: 1px solid rgba(252, 252, 252, 0);
        cursor: pointer;
        transition: 0.2s;
      }
      .selectTemplateClass {
        border: 1px solid #0079fd;
      }
    }
  }
  .advertisingType img:hover {
    border: 1px solid #ecf5ff;
  }
  .showClass {
    display: inline-block;
    width: 52px;
    height: 24px;
    color: #999999;
    font-size: 12px;
    text-align: center;
    margin-right: 24px;
  }
  .goodsUpload {
    background: #fcfcfc;
    color: #9a9a9a;
    width: 304px;
    height: 72px;
    text-align: center;
    line-height: 40px;
    border: 1px dashed #ddd;
    transition: 0.2s;
    cursor: pointer;
    border-radius: 2px;
    .el-upload__text {
      height: 20px;
      font-size: 12px;
      color: #999999;
      span {
        font-size: 22px;
        position: relative;
        right: 3px;
        top: 3px;
      }
    }
    .el-upload__text:first-child {
      color: #0079fd;
      margin-bottom: 8px;
    }
  }

  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }
  .bgsetting {
    ul {
      width: 304px;
      height: 134px;
      // height: 230px;
      background: rgba(252, 252, 252, 1);
      border-radius: 2px;
      margin-bottom: 10px;
      border: 1px solid rgba(204, 204, 204, 0.4);
      transition: 0.2s;
      position: relative;
      cursor: pointer;
      &:hover {
        border: 1px solid rgba(0, 121, 253, 1);
      }
      .lastDel {
        position: absolute;
        right: -8px;
        top: -8px;
        padding: 0;
        .delBtn {
          width: 16px;
          height: 16px;

          display: none;
        }
        .delBtnShow {
          display: block;
        }
      }
      .showImg {
        display: flex;
        .selectBg {
          display: inline-block;
          width: 74px;
          line-height: 52px;
        }
        .showImgbtn {
          display: flex;
          .replaceImg {
            text-align: left;
          }
          .el-button--mini {
            padding: 0;
            width: 64px;
            height: 24px;
            background: rgba(255, 255, 255, 1);
            border-radius: 2px;
            font-size: 12px;
            color: #666;
            margin-left: 11px;
          }
          .notis {
            font-size: 12px;
            color: #999;
            transform: scale(0.8);
          }
        }
      }
      li {
        font-size: 12px;
        color: rgba(153, 153, 153, 1);
        text-align: center;
        padding-top: 12px;
        img {
          width: 100px;
          height: 50px;
        }
      }
      .el-input {
        width: 214px;
        margin-left: 10px;
      }
    }
  }
}
.upload-demo {
  text-align: center;
}
</style>
